.pure-button {
  background-color: #f2f2f2;
  border-radius: 3px;
  color: #333;
  padding: 0.4em 1em; }
  .pure-button.pure-button-default {
    border: 1px solid #eaeaea;
    text-shadow: 0 1px 1px rgba(255, 255, 255, 0.5); }
    .pure-button.pure-button-default:hover {
      color: #333; }
  .pure-button.pure-button-success, .pure-button.pure-button-error, .pure-button.pure-button-warning, .pure-button.pure-button-secondary, .pure-button.pure-button-primary {
    color: white;
    text-shadow: 0 1px 1px rgba(0, 0, 0, 0.2); }
  .pure-button button,
  .pure-button input[type="button"],
  .pure-button input[type="reset"],
  .pure-button input[type="submit"] {
    -webkit-appearance: none;
    display: block;
    background: none;
    border: none;
    color: inherit; }

.pure-button-primary {
  background: #0078e7; }

.pure-button-success {
  background: #1cb841; }

.pure-button-error {
  background: #ca3c3c; }

.pure-button-warning {
  background: #df7514; }

.pure-button-secondary {
  background: #42b8dd; }

.pure-button-xs {
  font-size: 70%; }

.pure-button-s {
  font-size: 85%; }

.pure-button-l {
  font-size: 110%; }

.pure-button-xl {
  font-size: 125%; }

.pure-button-pill {
  border-radius: 40px;
  padding: 0.3em 1em; }

h1 a.pure-button-with-heading, h2 a.pure-button-with-heading, h3 a.pure-button-with-heading {
  margin-left: 20px;
  font-size: 15px;
  line-height: 1em;
  vertical-align: middle; }

* {
  box-sizing: border-box; }

.pure-g-padding > [class*="pure-u"] {
  box-sizing: border-box;
  padding: 0 30px; }

.container {
  margin: 0 auto;
  max-width: 1200px; }

.layout {
  margin-bottom: 50px; }

.pure-menu.menu-default ul {
  background: #ba3333;
  padding: 8px 10px;
  border-radius: 3px;
  font-size: 17px;
  width: 100%; }

.pure-menu.menu-default a {
  color: #fff; }
  .pure-menu.menu-default a:hover, .pure-menu.menu-default a:focus, .pure-menu.menu-default a.pure-menu-selected {
    border-radius: 3px;
    background: #7e2222;
    color: #fff; }

.pure-menu.menu-plain {
  border: 0;
  margin-top: 100px; }
  .pure-menu.menu-plain a {
    padding: 5px 10px;
    margin-left: -10px; }

.pure-menu.menu-plain-submenu {
  border: 0;
  background: none; }
  .pure-menu.menu-plain-submenu a {
    padding-left: 40px; }
  .pure-menu.menu-plain-submenu .menu-plain-submenu a {
    padding-left: 60px; }

.menu-trigger {
  transition: all 0.2s ease-out;
  float: right;
  border-radius: 3px;
  display: block;
  background: #fff;
  color: #ba3333;
  font-size: 3em;
  z-index: 10;
  margin-right: 20px;
  width: 50px;
  height: 50px;
  text-align: center; }
  .menu-trigger:visited {
    color: #ba3333; }
  .menu-trigger:hover, .menu-trigger:focus, .menu-trigger.active {
    background: #ba3333;
    color: #fff; }
  @media screen and (min-width: 480px) {
    .menu-trigger {
      display: none; } }

