/* Fonts */
@font-face {
  font-family: 'ion';
  src: url("../../../themes/pure-theme/fonts/ion/ion.eot?-uaazva");
  src: url("../../../themes/pure-theme/fonts/ion/ion.eot?#iefix-uaazva") format("embedded-opentype"), url("../../../themes/pure-theme/fonts/ion/ion.woff?-uaazva") format("woff"), url("../../../themes/pure-theme/fonts/ion/ion.ttf?-uaazva") format("truetype"), url("../../../themes/pure-theme/fonts/ion/ion.svg?-uaazva#ion") format("svg");
  font-weight: normal;
  font-style: normal; }

/*
* Typography specific to how silverstripe outputs CMS editable fields
* to be imported into the CMS
*/
iframe {
  border: 0; }

.captionImage .caption, .media .caption {
  font-style: italic; }

img {
  max-width: 100%;
  height: auto;
  display: block; }
  img.right {
    float: right;
    clear: right;
    margin: 18px 0 18px 80px; }
  img.left {
    float: left;
    clear: left;
    margin: 18px 80px 18px 0px; }
  img.leftAlone {
    margin: 18px 80px 18px 0;
    float: none;
    display: block;
    clear: both; }
  img.center {
    display: block;
    margin: 18px auto 18px auto;
    clear: both; }

/* Media insert formatting  (Youtube embed) */
.media.center {
  margin: 0 auto;
  text-align: center; }

.media.left {
  float: left;
  clear: left;
  margin: 0 18px 18px 0px; }

.media.leftAlone {
  margin: 0 18px 18px 0;
  float: none;
  display: block;
  clear: both; }

.media.right {
  float: right;
  clear: right;
  margin: 0 0 18px 18px; }

figure.featured-image img {
  margin-bottom: 0; }

.captionImage, figure.featured-image {
  max-width: 100%; }
  .captionImage.left, figure.featured-image.left {
    float: left;
    margin: 0 80px 40px 20px; }
  .captionImage.right, figure.featured-image.right {
    float: right;
    margin: 0 0 40px 80px; }
    .captionImage.right p, figure.featured-image.right p {
      margin: 0px 60px 40px 20px; }
  .captionImage.leftAlone, figure.featured-image.leftAlone {
    float: none;
    margin: 0 80px 40px 20px; }
  .captionImage.center, figure.featured-image.center {
    margin: 0 auto 40px; }
    .captionImage.center p, figure.featured-image.center p {
      margin: 0px 20px 40px 20px; }
  @media screen and (max-width: 750px) {
    .captionImage.left, .captionImage.right, .captionImage.leftAlone, .captionImage.center, figure.featured-image.left, figure.featured-image.right, figure.featured-image.leftAlone, figure.featured-image.center {
      margin-bottom: 40px;
      margin-top: 10px; }
      .captionImage.left p, .captionImage.right p, .captionImage.leftAlone p, .captionImage.center p, figure.featured-image.left p, figure.featured-image.right p, figure.featured-image.leftAlone p, figure.featured-image.center p {
        text-align: center;
        margin: 0px 0 40px 20px; } }
  .captionImage p, figure.featured-image p {
    clear: both;
    margin: 0px 0 40px 60px;
    color: #ccc;
    padding-top: 15px;
    font-weight: normal;
    font-family: "Nobile", helvetica, arial, sans-serif;
    font-size: 16px;
    line-height: 22px;
    padding-bottom: 2px; }
    @media screen and (max-width: 750px) {
      .captionImage p, figure.featured-image p {
        text-align: center;
        margin: 0px 20px 40px 20px; } }
  .captionImage img, figure.featured-image img {
    margin: 0 0 5px 0;
    border: 0; }

